.main {
    min-height: 100vh;
    width: 100%;
    background-color: #630094;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    letter-spacing: 1px;
    &__form {
      border: 1px solid #b768f7;
      border-radius: 3px;
      background-color: rgba(255, 255, 255, 0.4);
      width: 50rem;
      padding:2rem 5rem;
      display: flex;
      flex-direction: column;
      &_heading {
        align-self: center;
        margin: 1rem;
        font-size: 3rem;
      }
      &_label {
        font-size: 1.4rem;
        font-weight: 400;
        margin: 1rem 0;
      }
      &_input {
        padding: .5rem 1rem;
        border: none;
        font-size: 1.5rem;
        margin: 0.5rem 0;
        border-bottom: 1px solid transparent;
        &:active,
        &:focus {
          outline: none;
          border-bottom: 1px solid #630094;
        }
      }
      &_submit {
        margin-top: 2rem;
        padding: 1rem 5rem;
        background-color: #000;
        color: #fff;
        font-size: 2rem;
        cursor: pointer;
        border-radius: 1rem;
        align-self: center;
        transition: all 0.1s;
        &:hover {
          transform: translateY(-3px);
        }
      }
    }
  }
  